import { ToastController } from "ionic-angular";import {Injectable} from "@angular/core";@Injectable()export class CommonService {    constructor(public toastCtrl:ToastController) {}    /**     *  2s     * @param message       * @param callback       */    toast(message, callback?) {        let toast = this.toastCtrl.create({            message: message,            duration: 2000,            position: 'middle',            dismissOnPageChange: true,        });        toast.present();        if (callback) {            callback();        }    }}


