import {Injectable} from "@angular/core";@Injectable()export class DataFormatService {    constructor() {    }    /**     * JSONform     * @param json   json     * @returns {FormData}   formData     */    toFormData(json) {        let formData = new FormData();        for (let k in json) {            formData.append(k, json[k])        }        return formData;    }    /**     * JSON      * @param data  json     * @returns {string}     */    toQuery(data) {        let str = '?';        for (let key in data) {            if (data.hasOwnProperty(key)) {                const value = data[key];                str += key + '=' + value + '&';            }        }        str = str.substring(0, str.length - 1);        return str;    }}


