import {HttpClient} from "@angular/common/http";import {Injectable} from "@angular/core";import {Observable} from "rxjs/Observable";import {SERVER_API_URL} from "../../app/app.constants";import {HTTP} from "@ionic-native/http";import {DataFormatService} from "../../core/dataFormat.service";@Injectable()export class MineService {    constructor(private http: HttpClient, private nativeHttp: HTTP, private dataForm: DataFormatService) {    }    //    GetMyCollectionProductList(data): Observable<any> {        return this.http.post(SERVER_API_URL + '/EProduct/GetMyCollectionProductList', data)    }    //    GetMyProductCount(data): Observable<any> {        return this.http.post(SERVER_API_URL + '/EProduct/GetMyProductCount', data)    }    //    GetMyProductList(data): Observable<any> {        return this.http.post(SERVER_API_URL + '/EProduct/GetMyProductList', data)    }    //    GetUserNewsList(data): Observable<any> {        return this.http.post(SERVER_API_URL + '/appnews/GetUserNewsList', data)    }    //    GetNewsById(data): Observable<any> {        return this.http.get(SERVER_API_URL + '/appnews/GetNewsById' + this.dataForm.toQuery(data))    }    //    getMyScores(data): Observable<any> {        return this.http.post(SERVER_API_URL + '/study/getMyScoresP', data)    }    //    homeworkInit(data): Observable<any> {        return this.http.post(SERVER_API_URL + '/study/homeworkInit' + this.dataForm.toQuery(data), null);    }    //    saveStuExams(data): Observable<any> {        return this.http.post(SERVER_API_URL + '/study/saveStuExams', data);    }    //    submitStuExams(data): Observable<any> {        return this.http.post(SERVER_API_URL + '/study/submitStuExams', data);    }    //    getStuScore(data): Observable<any> {        return this.http.post(SERVER_API_URL + '/study/getStuScore' + this.dataForm.toQuery(data), null);    }}


